// style button in header//
.button-headers {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;

  &:hover,
  &:focus {
    background-color: #ff6b02;
    border: 1px solid #ff6b02;
  }

  @media screen and (min-width: 320px) {
    width: 130px;
    height: 44px;
  }

  @media screen and (min-width: 768px) {
    width: 152px;
  }

  @media screen and (min-width: 1200px) {
    width: 147px;
    height: 42px;
  }
}

// style button in modal window//

.button-modal {
  border-radius: 5px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;

  @media screen and (min-width: 320px) {
    width: 110px;
    height: 44px;
  }

  @media screen and (min-width: 768px) {
    width: 125px;
  }

  @media screen and (min-width: 1200px) {
    width: 136px;
  }

    &:hover,
    &:focus {
      background-color: var(--accent-color);
      border-color: var(--accent-color);
      color: var(--first-text-color);
    }
  
    &.active {
      background-color: var(--accent-color);
      border-color: var(--accent-color);
      color: var(--first-text-color);
      transform: scale(1.05);
    }
}

